/* Global font settings - Manrope as default, Satoshi for hero */

:root {
  /* Figma Design System Colors */
  --color-primary: #143197;              /* Primary Blue */
  --color-secondary: #EB5857;            /* Secondary Red */
  --color-success: #1FBB65;              /* Success Green */
  --color-warning: #FFE81A;              /* Warning Yellow */
  --color-info: #57C7EB;                 /* Info Blue */
  --color-purple: #6941C6;               /* Purple */
  --color-orange: #FF9772;               /* Orange */
  
  /* Neutrals */
  --color-black: #000000;
  --color-gray-dark: #434242;
  --color-gray-medium: #454343;
  --color-gray: #5F5F5F;
  --color-gray-light: #ADADAD;
  --color-gray-lighter: #C9C9C9;
  --color-gray-border: #E9E9E9;
  --color-gray-bg: #F1F1F1;
  --color-gray-bg-light: #F6F6F6;
  --color-white: #FFFFFF;
  
  /* Typography */
  --font-primary: 'Manrope', sans-serif;
  --font-brand: 'Manrope', sans-serif;
  --font-hero: 'Satoshi', sans-serif;
  
  /* Shadows */
  --shadow-card: 0px 2px 5px 3px rgba(215, 215, 215, 0.1);
  --shadow-modal: 0px 15px 40px 5px rgba(136, 136, 136, 0.42);
  --shadow-button-red: 1px 2px 10px #DDA1A1;
  --shadow-button-dark: 1px 2px 10px #5F5F5F;
  
  /* Enhanced Elevation System */
  --shadow-elevation-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-elevation-base: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-elevation-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-elevation-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-elevation-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-elevation-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  
  /* Convert to HSL for CSS variables compatibility */
  --krushr-primary: 225 77% 34%;          /* #143197 */
  --krushr-secondary: 0 79% 63%;          /* #EB5857 - Coral Red (Accent) */
  --krushr-success: 141 82% 43%;          /* #1FBB65 */
  --krushr-warning: 32 94% 43%;           /* #D97706 */
  --krushr-info: 194 83% 64%;             /* #57C7EB */
  --krushr-purple: 259 57% 52%;           /* #6941C6 */
  --krushr-orange: 16 100% 72%;           /* #FF9772 */
  --krushr-coral-red: 0 79% 63%;          /* #EB5857 - Accent color */
  --krushr-gray-dark: 2 2% 26%;           /* #434242 */
  --krushr-gray: 0 0% 37%;                /* #5F5F5F */
  --krushr-gray-light: 0 0% 68%;          /* #ADADAD */
  
  /* Design System Colors (Figma-based) */
  --background: 0 0% 100%;                /* White */
  --foreground: var(--krushr-gray-dark);  /* #434242 */
  --card: 0 0% 100%;                      /* White */
  --card-foreground: var(--krushr-gray-dark);
  --popover: 0 0% 100%;
  --popover-foreground: var(--krushr-gray-dark);
  --primary: var(--krushr-primary);       /* #143197 Blue as Primary */
  --primary-foreground: 0 0% 100%;
  --secondary: var(--krushr-secondary);   /* #EB5857 Red as Secondary */
  --secondary-foreground: 0 0% 100%;
  --muted: 0 0% 94.5%;                    /* #F1F1F1 */
  --muted-foreground: var(--krushr-gray);
  --accent: var(--krushr-coral-red);      /* #EB5857 Coral Red */
  --accent-foreground: 0 0% 100%;
  --destructive: var(--krushr-secondary); /* #EB5857 */
  --destructive-foreground: 0 0% 100%;
  --border: 0 0% 91%;                     /* #E9E9E9 */
  --input: 0 0% 91%;                      /* #E9E9E9 */
  --ring: var(--krushr-primary);         /* #143197 */
  
  /* Krushr-themed Charts */
  --chart-1: var(--krushr-coral-red);     /* Primary Coral Red */
  --chart-2: var(--krushr-blue);          /* Supporting Blue */
  --chart-3: var(--krushr-light-blue);
  --chart-4: var(--krushr-sky-blue);
  --chart-5: 0 31% 68%;                   /* Medium Coral */
  
  --radius: 0.5rem;
  
  /* Figma-based Sidebar */
  --sidebar-background: 0 0% 97%;         /* #F6F6F6 Light Background */
  --sidebar-foreground: var(--krushr-gray-dark); /* #434242 Dark Gray */
  --sidebar-icon: var(--krushr-secondary); /* #EB5857 Red for icons */
  --sidebar-primary: var(--krushr-primary); /* #143197 Blue */
  --sidebar-primary-foreground: 0 0% 100%;
  --sidebar-accent: 0 0% 94.5%;           /* #F1F1F1 */
  --sidebar-accent-foreground: var(--krushr-gray-dark);
  --sidebar-border: 0 0% 91%;             /* #E9E9E9 */
  --sidebar-ring: var(--krushr-primary);
}

.dark {
  /* Dark mode with Krushr Coral Red branding */
  --background: 0 15% 6%;                 /* Dark coral-tinted background */
  --foreground: 0 0% 98%;
  --card: 0 15% 8%;                       /* Slightly lighter dark coral */
  --card-foreground: 0 0% 98%;
  --popover: 0 15% 8%;
  --popover-foreground: 0 0% 98%;
  --primary: var(--krushr-coral-red);     /* Coral Red stays primary in dark */
  --primary-foreground: 0 0% 98%;
  --secondary: 0 15% 12%;
  --secondary-foreground: 0 0% 98%;
  --muted: 0 0% 14.9%;
  --muted-foreground: 0 0% 63.9%;
  --accent: 0 0% 14.9%;
  --accent-foreground: 0 0% 98%;
  --destructive: 0 62.8% 30.6%;
  --destructive-foreground: 0 0% 98%;
  --border: 0 0% 14.9%;
  --input: 0 0% 14.9%;
  --ring: 0 0% 83.1%;
  --chart-1: 220 70% 50%;
  --chart-2: 160 60% 45%;
  --chart-3: 30 80% 55%;
