html {
  scroll-behavior: smooth;
  font-size: 16px;
}

body {
  color: hsl(var(--foreground));
  background-color: hsl(var(--background));
  overscroll-behavior: none;
  font-synthesis-weight: none;
  font-family: var(--font-primary); /* Manrope as primary */
}

html > body * {
  border-color: hsl(var(--border));
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: hsl(var(--border));
  border-radius: 5px;
}

* {
  scrollbar-width: thin;
  scrollbar-color: hsl(var(--border)) transparent;
}

/* Figma Design System Implementation */

/* Primary font (Manrope) for UI elements */
body, .ui-text, .body-text, input, button, label {
  font-family: var(--font-primary) !important;
}

/* Brand font (Manrope) for brand elements */
.brand-text, .logo-text, .brand-heading, h1.brand, h2.brand {
  font-family: var(--font-brand) !important;
}

/* Figma Typography Scale */
.text-micro {
  font-family: var(--font-primary);
  font-size: 3px;
  line-height: 4px;
}

.text-tiny {
  font-family: var(--font-primary);
  font-size: 4.5px;
  line-height: 5px;
}

.text-small {
  font-family: var(--font-primary);
  font-size: 6px;
  line-height: 8px;
}

.text-body {
  font-family: var(--font-primary);
  font-size: 7px;
  line-height: 10px;
}

.text-label {
  font-family: var(--font-primary);
  font-size: 8px;
  line-height: 11px;
  font-weight: 600;
}

.text-heading {
  font-family: var(--font-primary);
  font-size: 20px;
  line-height: 27px;
  font-weight: 600;
}

.text-brand-logo {
