  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  color: var(--color-primary);
}

.text-brand-tagline {
  font-family: var(--font-brand);
  font-size: 4.5px;
  line-height: 5px;
  font-weight: 500;
  color: var(--color-gray-light);
}

/* Figma Component Styles */
.figma-card {
  background: var(--color-white);
  border: 0.5px solid var(--color-gray-border);
  box-shadow: var(--shadow-card);
  border-radius: 5px;
}

.figma-modal {
  background: var(--color-white);
  box-shadow: var(--shadow-modal);
  border-radius: 5px;
}

.figma-button-primary {
  background: var(--color-primary);
  border: 0.7px solid var(--color-primary);
  border-radius: 5px;
  color: var(--color-white);
  font-family: var(--font-primary);
  font-weight: 500;
}

.figma-button-secondary {
  background: var(--color-secondary);
  border: 0.7px solid var(--color-secondary);
  border-radius: 5px;
  color: var(--color-white);
  font-family: var(--font-primary);
  font-weight: 500;
}

.figma-button-outline {
  background: transparent;
  border: 0.7px solid var(--color-primary);
  border-radius: 5px;
  color: var(--color-primary);
  font-family: var(--font-primary);
  font-weight: 500;
}

.figma-tag-critical {
  background: var(--color-warning);
  border-radius: 5px;
  font-family: var(--font-primary);
  font-weight: 500;
  font-size: 5px;
  color: var(--color-white);
  padding: 2px 6px;
}

.figma-tag-package {
  background: var(--color-purple);
  border-radius: 5px;
  font-family: var(--font-primary);
  font-weight: 500;
  font-size: 5px;
  color: var(--color-white);
  padding: 2px 6px;
}

.figma-tag-location {
  background: var(--color-primary);
  border-radius: 5px;
  font-family: var(--font-primary);
  font-weight: 500;
  font-size: 5px;
  color: var(--color-white);
  padding: 2px 6px;
}

.figma-priority-dot {
  width: 5px;
  height: 5px;
  border-radius: 3px;
}

.figma-priority-active {
  background: var(--color-secondary);
}

.figma-priority-inactive {
  background: var(--color-gray-lighter);
}

/* Keep sidebar icons red per Figma */
.sidebar .lucide, .sidebar svg, nav .lucide, nav svg {
  color: var(--color-secondary) !important; /* #EB5857 Red */
