  font-family: var(--font-primary);
  font-size: 7px;
  line-height: 10px;
  font-weight: 500;
  color: var(--color-gray);
  margin-bottom: 8px !important;
}

.board-actions {
  gap: 8px !important;
}

.board-search {
  padding: 6px 12px !important;
  font-family: var(--font-primary);
  font-size: 8px;
  border: 0.7px solid var(--color-gray-border);
  border-radius: 5px;
}

.board-search:focus {
  border-color: var(--color-secondary);
  outline: none;
  box-shadow: 0 0 0 1px var(--color-secondary);
}

/* Apply Figma font weights */
body {
  font-weight: 400; /* Figma standard weight */
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600; /* Figma heading weight */
}

.font-medium {
  font-weight: 500; /* Figma medium weight */
}

.font-semibold {
  font-weight: 600; /* Figma semi-bold weight */
}

.font-bold {
  font-weight: 700; /* Figma bold weight */
}

/* Accessibility & Mobile Touch Enhancements */

/* WCAG AA compliant touch targets - minimum 44x44px */
.touch-target {
  min-width: 44px !important;
  min-height: 44px !important;
  touch-action: manipulation;
}

.touch-target-small {
  min-width: 32px !important;
  min-height: 32px !important;
  touch-action: manipulation;
}

/* Panel drag handle touch behavior */
.panel-drag-handle {
  touch-action: none !important;
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  user-select: none !important;
}

/* Panel container touch behavior - prevent scrolling only during drag */
.panel-container {
  overscroll-behavior: none !important;
}

.panel-card {
  overscroll-behavior: none !important;
}

/* React Grid Layout touch behavior - allow vertical scrolling */
.react-grid-layout {
  overscroll-behavior: none !important;
  touch-action: pan-y !important; /* Allow vertical scrolling */
}

.react-grid-item {
  touch-action: pan-y !important; /* Allow vertical scrolling */
  overscroll-behavior: none !important;
}

.react-grid-item.react-grid-placeholder {
  touch-action: pan-y !important;
}

.react-grid-item.cssTransforms {
  touch-action: pan-y !important;
}

/* Only disable touch during actual dragging */
.react-grid-item.react-draggable-dragging {
  touch-action: none !important;
}

/* Resize handles should allow vertical scrolling */
.react-resizable-handle {
  touch-action: pan-y !important;
}

/* Enhanced focus indicators for accessibility */
.focus-ring {
  transition: all 0.2s ease-in-out;
}

.focus-ring:focus {
  outline: none !important;
  box-shadow: 0 0 0 2px hsl(var(--ring)) !important;
  border-color: hsl(var(--ring)) !important;
}

.focus-ring:focus-visible {
  outline: none !important;
  box-shadow: 0 0 0 2px hsl(var(--ring)) !important;
  border-color: hsl(var(--ring)) !important;
}

/* Improved hover states for better interaction feedback */
.hover-lift:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.hover-bg:hover {
  background-color: hsl(var(--muted)) !important;
}

.hover-scale:hover {
  transform: scale(1.02);
}

/* Mobile-first responsive utilities */
@media (max-width: 768px) {
  /* Ensure all interactive elements meet touch target size */
  button, 
  [role="button"],
  input[type="checkbox"],
  input[type="radio"],
  select,
  .interactive {
    min-width: 44px;
    min-height: 44px;
    touch-action: manipulation;
  }
  
  /* Larger tap areas for small icons on mobile */
  .mobile-touch {
    padding: 12px !important;
  }
  
  /* Better spacing for mobile readability */
  .mobile-spacing {
    margin: 8px 0 !important;
  }
  
  /* Readable text sizes on mobile */
  .mobile-text {
    font-size: 16px !important;
    line-height: 1.5 !important;
  }
}

/* Skip link for keyboard navigation */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: hsl(var(--primary));
  color: hsl(var(--primary-foreground));
  padding: 8px;
  border-radius: 4px;
  text-decoration: none;
  z-index: 1000;
  font-weight: 600;
  font-size: 14px;
  transition: top 0.3s;
}

.skip-link:focus {
  top: 6px;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .border {
    border-width: 2px !important;
  }
  
  .focus-ring:focus,
  .focus-ring:focus-visible {
    box-shadow: 0 0 0 3px hsl(var(--ring)) !important;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

